import Joi from "joi";

const keys = {
  produkId: Joi.string().empty().messages({
    "string.empty": "Pilih jenis vaksinasi.",
  }),
  jenisId: Joi.string().empty().messages({
    "string.empty": "Isi Jenis identitas anda.",
  }),
  jenisIdnomor: Joi.string().empty().min(6).messages({
    "string.empty": "Isi nomor identitas anda.",
    "string.min": "Identitas min 6 karakter.",
  }),
  namaLengkap: Joi.string().empty().min(3).messages({
    "string.empty": "Isi nama lengkap anda.",
    "string.min": "Nama lengkap min 3 karakter.",
  }),
  tempatLahir: Joi.string().empty().min(3).messages({
    "string.empty": "Isi tempat kelahiran anda.",
    "string.min": "tempat kelahiran min 3 karakter.",
  }),
  tglLahir: Joi.string().empty().messages({
    "string.empty": "Isi tanggal kelahiran anda.",
  }),
  jenisKelamin: Joi.any().empty().messages({
    "any.empty": "Isi jenis kelamin anda.",
  }),
  alamatDomisili: Joi.string().empty().min(3).messages({
    "string.empty": "Isi alamat anda.",
    "string.min": "alamat min 3 karakter.",
  }),
  rt: Joi.string().empty().min(2).messages({
    "string.empty": "Isi rt anda.",
  }),
  rw: Joi.string().empty().min(2).messages({
    "string.empty": "Isi rw anda.",
  }),
  kelurahan: Joi.string().empty().min(3).messages({
    "string.empty": "Isi alamat anda.",
    "string.min": "alamat min 3 karakter.",
  }),
  kecamatan: Joi.string().empty().min(3).messages({
    "string.empty": "Isi alamat anda.",
    "string.min": "alamat min 3 karakter.",
  }),
  propinsi: Joi.string().empty().min(3).messages({
    "string.empty": "Isi propinsi anda.",
    "string.min": "propinsi min 3 karakter.",
  }),
  kotaDati: Joi.string().empty().min(3).messages({
    "string.empty": "Isi kotamadya anda.",
    "string.min": "kotamadya min 3 karakter.",
  }),
  kodePos: Joi.string().empty().min(3).messages({
    "string.empty": "Isi kodepos anda.",
    "string.min": "kodepos min 3 karakter.",
  }),
  alamat: Joi.string().empty().min(3).messages({
    "string.empty": "Isi kotamadya anda.",
    "string.min": "alamat min 3 karakter.",
  }),
  noTelpon: Joi.string().empty().min(10).messages({
    "string.empty": "Isi noTelpon anda.",
    "string.min": "noTelpon min 10 karakter.",
  }),
  agama: Joi.any().empty().messages({
    "any.empty": "Isi agama anda.",
  }),
};

export function isFormValid(data) {
  const schema = Joi.object(keys);

  //Object.assign(data, { spouseIdNo: data.marriageStatus === 'MENIKAH' ? data.spouseIdNo : '' });
  //Object.assign(data, { partnerName: data.marriageStatus === 'MENIKAH' ? data.partnerName : '' });

  return schema.validate(data);
}

export function isSelectedFieldValid(field, value) {
  if(keys[field]){
  const schema = Joi.object({ field: keys[field] });

  return schema.validate({
    field: value,
  });
  }
  return true;
}
