import React from "react";
import Stack from "@mui/material/Stack";
import InputLabel from "@mui/material/InputLabel";
import FormControl from "@mui/material/FormControl";
import MenuItem from "@mui/material/MenuItem";
import Select from "@mui/material/Select";
import TextField from "@mui/material/TextField";
import Button from "@mui/material/Button";
import Container from "@mui/material/Container";
import {
  CustomDialog,
  CustomInput,
  ImgContribute,
  TopNav,
} from "../../components";
import { useNavigate } from "react-router-dom";
import makeStyles from "@mui/styles/makeStyles";
import { connect } from "react-redux";
import {
  getAgama,
  getJenisIdentitas,
  getStatusNikah,
  register,
  getDataProduk,
  setFormVaksin,
  resetVaksin,
  setIdentitas,
  getIdentitas,
  registerVaksin,
} from "../../redux/actions";
import { isFormValid, isSelectedFieldValid } from "./validation";
import languageImg from "../../assets/language.gif";
import {
  Checkbox,
  Dialog,
  FormControlLabel,
  FormGroup,
  FormLabel,
  Grid,
  Radio,
  RadioGroup,
  Typography,
} from "@mui/material";

const useStyles = makeStyles(() => ({
  root: {
    padding: "16px 0",
  },
  langContainer: {
    padding: "16px 0",
    overflowX: "hidden",
  },
  dialogImg: {
    height: "250px",
    width: "auto",
  },
}));
const RegisterVaksin = (props) => {
  const {
    actGetAgama,
    actGetJenisID,
    actGetStatusNikah,
    stateAgama,
    stateStatusNikah,
    stateJenisID,
    stateError,
    actRegisterVaksin,
    actGetProduk,
    stateProduk,
    actSetFormVaksin,
    stateRegVaksin,
    actResetVaksin,
    actSetIdentitas,
    stateIdentitas,
    actGetIdentitas,
  } = props;
  const {
    jenisId,
    jenisIdnomor,
    namaLengkap,
    tempatLahir,
    tglLahir,
    jenisKelamin,
    alamat,
    rt,
    rw,
    kelurahan,
    kecamatan,
    kotaDati,
    kodePos,
    propinsi,
    noTelpon,
    agama,
    statusNikah,
    alamatDomisili,
  } = stateIdentitas;

  const { produkId } = stateRegVaksin;

  const invalidInitial = {
    jenisId: "",
    produkId: "",
    jenisIdnomor: "",
    namaLengkap: "",
    tempatLahir: "",
    tglLahir: "",
    jenisKelamin: "",
    alamat: "",
    rt: "",
    rw: "",
    kelurahan: "",
    kecamatan: "",
    kotaDati: "",
    kodePos: "",
    propinsi: "",
    noTelpon: "",
    agama: "",
    statusNikah: "",
    alamatDomisili: "",
  };

  const classes = useStyles();
  const [openFail, setOpenFail] = React.useState(false);
  const [sameAddress, setHandleSameAddress] = React.useState(false);
  const [errorForm, setErrorForm] = React.useState("");
  const [succForm, setSuccForm] = React.useState(false);

  //   const [form, setForm] = React.useState(formInitial);
  const [formInvalid, setInvalid] = React.useState(invalidInitial);
  const [bahasa, setBahasa] = React.useState("");
  const [openBahasa, setOpenBahasa] = React.useState(true);

  const navigate = useNavigate();
  React.useEffect(() => {
    if (stateError) {
      navigate("/exception-handling");
    }
  }, [stateError]);

  React.useEffect(() => {
    actGetAgama();
    actGetJenisID();
    actGetStatusNikah();
    actGetProduk();
  }, []);

  const changeForm = (elem, type) => {
    let { name, id, value } = elem.target;
    let { error } = isSelectedFieldValid(name ? name : id, value);
    setInvalid({
      ...formInvalid,
      [name ? name : id]: error ? "*" + error.message : "",
    });
    if (type === "identitas") {
      actSetIdentitas({
        key: name ? name : id,
        value: value,
      });
    } else {
      actSetFormVaksin({
        key: name ? name : id,
        value: value,
      });
    }
    // return setForm({
    //   ...form,
    //   [name ? name : id]: value,
    // });
    // return;
  };
  const handleGetNIK = () => {
    if (jenisId === "1" && jenisIdnomor.length === 16) {
      actGetIdentitas(jenisIdnomor);
    }
  };
  const [submitted, setSubmitted] = React.useState(false);
  const handleSubmit = () => {
    let body = Object.assign(stateRegVaksin, stateIdentitas);
    let isValid = isFormValid(body);
    setSubmitted(true);
    if (!isValid.error) {
      return actRegisterVaksin(body, {
        success: () => {
          actResetVaksin();
          setSuccForm(true);
          setSubmitted(false);
        },
        failed: (msg) => {
          setErrorForm(msg);
          setSubmitted(false);
        },
      });
    } else {
      if (
        isValid.error.details[0].context &&
        isValid.error.details[0].context.key
      ) {
        let key = isValid.error.details[0].context.key;
        setInvalid({
          ...formInvalid,
          [key]: isValid.error.details[0].message,
        });
      }
      setErrorForm(isValid.error.details[0].message || "Mohon Lengkapi Form!.");
      setSubmitted(false);
    }
  };
  const handleBahasa = (val) => {
    setOpenBahasa(false);
    return setBahasa(val);
  };

  const handleSameAddress = () => {
    setHandleSameAddress(!sameAddress);
    // setForm({ ...form, alamat: alamatDomisili });
  };
  return (
    <>
      <CustomDialog
        setOpen={setSuccForm}
        type={"success"}
        title={"Berhasil Terdaftar!."}
        open={succForm}
      />
      <CustomDialog
        setOpen={setErrorForm}
        type={"error"}
        title={errorForm}
        open={errorForm ? true : false}
      />
      <Dialog open={openBahasa}>
        <Container className={classes.langContainer}>
          <Grid container spacing={2}>
            <Grid xs={12} item container justifyContent={"center"}>
              <ImgContribute
                title="People illustrations by Storyset"
                src={languageImg}
                alt="Contribute"
                link="https://storyset.com/people"
              />
            </Grid>
            <Grid xs={12} item>
              <Typography align="center" variant="body1">
                Bahasa yang digunakan / Language
              </Typography>
            </Grid>
            <Grid xs={12} item>
              <Button
                variant="contained"
                onClick={() => handleBahasa("indonesia")}
              >
                Bahasa Indonesia
              </Button>
            </Grid>
            <Grid xs={12} item>
              <Button
                variant="outlined"
                onClick={() => handleBahasa("english")}
              >
                English
              </Button>
            </Grid>
          </Grid>
        </Container>
      </Dialog>
      <Container className={classes.root}>
        <Stack spacing={2}>
          <TopNav
            type="back"
            title="Daftar Vaksinasi"
            click={() => navigate(-1)}
          />
          <FormControl
            fullWidth
            required
            error={formInvalid.produkId ? true : false}
          >
            <InputLabel id="produkId">
              {bahasa === "indonesia" ? "Jenis Vaksin" : "Vaccine Type"}
            </InputLabel>
            <Select
              labelId="produkId"
              id="produkId"
              name="produkId"
              value={produkId}
              onChange={(el) => changeForm(el, "vaksin")}
            >
              {stateProduk &&
                stateProduk.map((item) => {
                  return (
                    <MenuItem
                      key={"vaksin" + item.ref_prod_nama}
                      value={item.ref_prod_nama}
                    >
                      {item.ref_prod_nama}
                    </MenuItem>
                  );
                })}
            </Select>
          </FormControl>

          <FormControl
            fullWidth
            required
            error={formInvalid.jenisId ? true : false}
          >
            <InputLabel id="jenisId">
              {bahasa === "indonesia" ? "Jenis Identitas" : "Identity Type"}
            </InputLabel>
            <Select
              labelId="jenisId"
              id="jenisId"
              name="jenisId"
              value={jenisId}
              onChange={(el) => changeForm(el, "identitas")}
            >
              {stateJenisID &&
                stateJenisID.map((item) => {
                  return (
                    <MenuItem
                      key={"jenisID" + item.ref_jki_id}
                      value={item.ref_jki_id.toString()}
                    >
                      {item.ref_jki_ket}
                    </MenuItem>
                  );
                })}
            </Select>
          </FormControl>
          <TextField
            id="jenisIdnomor"
            name="jenisIdnomor"
            label={bahasa === "indonesia" ? "No Identitas" : "Identity Number"}
            required
            error={formInvalid.jenisIdnomor ? true : false}
            value={jenisIdnomor}
            onChange={(el) => changeForm(el, "identitas")}
            onBlur={handleGetNIK}
            helperText={formInvalid.jenisIdnomor}
          />
          <TextField
            id="namaLengkap"
            name="namaLengkap"
            label={bahasa === "indonesia" ? "Nama Lengkap" : "Fullname"}
            required
            value={namaLengkap}
            error={formInvalid.namaLengkap ? true : false}
            onChange={(el) => changeForm(el, "identitas")}
            helperText={formInvalid.namaLengkap}
          />
          <TextField
            id="tempatLahir"
            name="tempatLahir"
            label={bahasa === "indonesia" ? "Tempat Lahir" : "Birthplace"}
            // label="Tempat Lahir - Birthplace"
            required
            value={tempatLahir}
            onChange={(el) => changeForm(el, "identitas")}
            error={formInvalid.tempatLahir ? true : false}
            helperText={formInvalid.tempatLahir}
          />
          <TextField
            id="tglLahir"
            name="tglLahir"
            label={bahasa === "indonesia" ? "Tanggal Lahir" : "Birthdate"}
            // label="Tanggal Lahir - Birthdate"
            required
            InputLabelProps={{
              shrink: true,
            }}
            type={"date"}
            value={tglLahir}
            onChange={(el) => changeForm(el, "identitas")}
            error={formInvalid.tglLahir ? true : false}
            helperText={formInvalid.tglLahir}
          />
          <FormControl required>
            <FormLabel id="jenisKelamin">
              {bahasa === "indonesia" ? "Jenis Kelamin" : "Gender"}
            </FormLabel>
            {/* <FormLabel id="jenisIdentitas">Jenis Kelamin - Gender</FormLabel> */}
            <RadioGroup
              row
              aria-labelledby="jeniKelamin"
              id="jenisKelamin"
              name="jenisKelamin"
              value={jenisKelamin}
              onChange={(el) => changeForm(el, "identitas")}
            >
              <FormControlLabel
                value="1"
                control={<Radio />}
                label={bahasa === "indonesia" ? "Pria" : "Male"}
                // label="Pria - Male"
              />
              <FormControlLabel
                value="2"
                control={<Radio />}
                label={bahasa === "indonesia" ? "Perempuan" : "Female"}

                // label="Perempuan - Female"
              />
            </RadioGroup>
          </FormControl>

          <TextField
            fullWidth
            id="propinsi"
            name="propinsi"
            // label="Provinsi - Province"
            label={bahasa === "indonesia" ? "Provinsi" : "Province"}
            required
            value={propinsi}
            onChange={(el) => changeForm(el, "identitas")}
            helperText={formInvalid.propinsi}
            error={formInvalid.propinsi ? true : false}
          />
          <TextField
            fullWidth
            id="kecamatan"
            // label="Kecamatan - Country"
            label={bahasa === "indonesia" ? "Kecamatan" : "Country"}
            required
            value={kecamatan}
            onChange={(el) => changeForm(el, "identitas")}
            helperText={formInvalid.kecamatan}
            error={formInvalid.kecamatan ? true : false}
          />
          <TextField
            fullWidth
            id="kotaDati"
            name="kotaDati"
            // label="Kotamadya / Dati II - Regency"
            label={bahasa === "indonesia" ? "Kotamadya / Dati II" : "Regency"}
            required
            value={kotaDati}
            onChange={(el) => changeForm(el, "identitas")}
            helperText={formInvalid.kotaDati}
            error={formInvalid.kotaDati ? true : false}
          />
          <TextField
            fullWidth
            id="kelurahan"
            name="kelurahan"
            // label="Kelurahan - Subdistrict"
            label={bahasa === "indonesia" ? "Kelurahan" : "Subdistrict"}
            required
            value={kelurahan}
            onChange={(el) => changeForm(el, "identitas")}
            helperText={formInvalid.kelurahan}
            error={formInvalid.kelurahan ? true : false}
          />
          <TextField
            fullWidth
            id="kodePos"
            name="kodePos"
            // label="Kode Pos - Post Code"
            label={bahasa === "indonesia" ? "Kode Pos" : "Post Code"}
            required
            value={kodePos}
            onChange={(el) => changeForm(el, "identitas")}
            helperText={formInvalid.kodePos}
            error={formInvalid.kodePos ? true : false}
          />
          <TextField
            fullWidth
            id="alamat"
            name="alamat"
            // label="Alamat Sesuai Identitas - Address ID"
            label={
              bahasa === "indonesia" ? "Alamat Sesuai Identitas" : "Address ID"
            }
            required
            value={alamat}
            onChange={(el) => changeForm(el, "identitas")}
            helperText={formInvalid.alamat}
            error={formInvalid.alamat ? true : false}
          />
          <div>
            <Stack direction="row" spacing={2}>
              <TextField
                fullWidth
                id="rt"
                name="rt"
                label="RT"
                required
                value={rt}
                onChange={(el) => changeForm(el, "identitas")}
                helperText={formInvalid.rw}
                error={formInvalid.rt ? true : false}

                // value={type}
                // onChange={changeType}
              />
              <TextField
                fullWidth
                id="rw"
                name="rw"
                label="RW"
                required
                value={rw}
                onChange={(el) => changeForm(el, "identitas")}
                helperText={formInvalid.rw}
                error={formInvalid.rw ? true : false}
              />
            </Stack>
          </div>
          <Stack>
            <TextField
              id="alamatDomisili"
              name="alamatDomisili"
              // label="Alamat Domisili - Current Address"
              label={
                bahasa === "indonesia" ? "Alamat Domisili" : "Current Address"
              }
              required
              error={formInvalid.alamatDomisili ? true : false}
              value={sameAddress ? alamat : alamatDomisili}
              disabled={sameAddress}
              onChange={(el) => changeForm(el, "identitas")}
              helperText={formInvalid.alamatDomisili}
            />
            <FormGroup>
              <FormControlLabel
                control={
                  <Checkbox
                    checked={sameAddress}
                    onChange={handleSameAddress}
                  />
                }
                label={
                  bahasa === "indonesia"
                    ? "Sama dengan alamat KTP"
                    : "Same as ID address"
                }
              />
            </FormGroup>
          </Stack>
          <Stack direction="row" spacing={2}>
            <CustomInput
              id="noTelpon"
              name="noTelpon"
              label={bahasa === "indonesia" ? "No Telp 1" : "Phone Number 1"}
              fullWidth
              required
              value={noTelpon}
              onChange={(el) => changeForm(el, "identitas")}
              helperText={formInvalid.noTelpon}
              className={classes.inputPhone}
              inputType="phone-number"
              error={formInvalid.noTelpon ? true : false}
            />
          </Stack>
          <FormControl fullWidth required>
            {/* <InputLabel id="agama">Agama - Religion</InputLabel> */}
            <InputLabel id="agama">
              {bahasa === "indonesia" ? "Agama" : "Religion"}
            </InputLabel>
            <Select
              labelId="agama"
              id="agama"
              name="agama"
              value={agama}
              onChange={(el) => changeForm(el, "identitas")}

              //   value={age}
              //   label="Age"
              //   onChange={handleChange}
            >
              {stateAgama &&
                stateAgama.map((item) => {
                  return (
                    <MenuItem
                      key={"agama" + item.ref_agama_id}
                      value={item.ref_agama_id}
                    >
                      {item.ref_agama_ket}
                    </MenuItem>
                  );
                })}
            </Select>
          </FormControl>
          <FormControl fullWidth required>
            {/* <InputLabel id="agama">
              Status Pernikahan - Marital Status
            </InputLabel> */}
            <InputLabel id="agama">
              {bahasa === "indonesia" ? "Status Pernikahan" : "Marital Status"}
            </InputLabel>
            <Select
              labelId="statusNikah"
              id="statusNikah"
              name="statusNikah"
              value={statusNikah}
              onChange={(el) => changeForm(el, "identitas")}
            >
              {stateStatusNikah &&
                stateStatusNikah.map((item) => {
                  return (
                    <MenuItem
                      key={"jenisID" + item.stat_id}
                      value={item.stat_id}
                    >
                      {item.stat_ket}
                    </MenuItem>
                  );
                })}
            </Select>
          </FormControl>
          <Button
            variant="outlined"
            onClick={() => setOpenBahasa(true)}
            // onClick={() => navigate("/")}
          >
            Ganti Bahasa / Change Language
          </Button>
          <Button
            variant="contained"
            onClick={handleSubmit}
            disabled={submitted}
            // onClick={() => navigate("/")}
          >
            {bahasa === "indonesia" ? "Daftar" : "Submit"}
          </Button>
        </Stack>
      </Container>
    </>
  );
};

const mapStateToProps = (state) => {
  return {
    stateProduk: state.master.produk,
    stateIdentitas: state.auth.identitas,
    stateAgama: state.master.agama,
    stateJenisID: state.master.jenisIdentitas,
    stateStatusNikah: state.master.statusNikah,
    stateRegVaksin: state.register.vaksin,
    stateError: state.app.error,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    actResetVaksin: () => dispatch(resetVaksin()),
    actGetIdentitas: (nik) => dispatch(getIdentitas(nik)),
    actSetFormVaksin: (body) => dispatch(setFormVaksin(body)),
    actSetIdentitas: (body) => dispatch(setIdentitas(body)),
    actGetProduk: (action) => dispatch(getDataProduk(action)),
    actGetAgama: (action) => dispatch(getAgama(action)),
    actGetJenisID: (action) => dispatch(getJenisIdentitas(action)),
    actGetStatusNikah: (action) => dispatch(getStatusNikah(action)),
    actRegisterVaksin: (body, action) => dispatch(registerVaksin(body, action)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(RegisterVaksin);
